.list {
	padding: 15px 40px;
	overflow-x: hidden;
	overflow-y: auto;
	display: flex;
	flex-direction: column;
	border-radius: 5px;
	border: 1px solid var(--borderColor);

	.item {
		display: flex;
		align-items: center;
		padding: 10px 0;

		&:not(:last-child) {
			border-bottom: 1px solid var(--borderColor);
		}

		span {
			width: 100%;
			max-width: 40%;
			font-size: 16px;
			font-style: normal;
			font-weight: 400;
			line-height: normal;
			display: block;
		}

		strong {
			display: block;
			flex: 1;
			font-size: 17px;
			font-style: normal;
			font-weight: 700;
			line-height: 21px;
		}
	}
}
